.btn-bg, .action-href, .href-hover, .btn-login, .search button, .header__bottom-menu, .item__action-btn, .btn-more, .btn-review, .btn-delete {
    background: none;
    border: none;
    padding: 0;
    font-family: var(--titleFont);
    color: inherit;
    font-size: inherit;
    cursor: pointer;
    transition: .2s;
    &:focus {
        outline: none;
    }
}
.action-href {
    position: relative;
    .icon-1-2_heart {
        font-size: 18px;
    }
    .icon-1-1_basket {
        font-size: 20px;
    }
}
a.heart {
    &:hover, &:focus {
        color: var(--Red);
    }
}
a.basket, .href-hover {
    &:hover, &:focus {
        color: var(--Grey);
    }
}
a.href-hover {
    &:hover, &:focus {
        color: var(--Grey);
    }
    &-dark {
        &:hover, &:focus {
            color: var(--mainColor);
        }
    }
}
.search, .btn-login, .header__logo, .item__action input + label {
    display: flex;
    align-items: center;
    justify-content: center;
}
.btn-login {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 20px;
    height: 20px;
    background-color: #c4c4c4;
    border-radius: 50%;
    margin-left: 30px;
    font-size: 10px;
    &::before {
        position: absolute;
        content: "";
        width: 1px;
        height: 20px;
        background-color: var(--borderColor);
        top: 0;
        left: -14.5px;
    }
}
@media(max-width: 450px) {
    .btn-login {
        margin-left: 20px;
        &::before {
            left: -10.5px;
        }
    }
}
.search {
    position: relative;
    height: 30px;
    input {
        position: absolute;
        top: 0;
        right: -10px;
        width: 0;
        height: 30px;
        border-radius: 15px;
        padding: 5px 10px;
        border: none;
        opacity: 0;
        transition: .5s;
    }
    i {
        font-size: 20px;
    }
    button {
        position: absolute;
        right: 0;
        top: 5px;
    }
    &.show {
        input {
            width: 200px;
            opacity: 1;
        }
    }
}
.btn-more, a.btn-more {
    position: relative;
    font-size: 22px;
    margin: 55px auto 0;
    display: table;
    &::after {
        content: "";
        height: 1px;
        width: 100%;
        background-color: var(--mainColor);
        position: absolute;
        left: 0;
        top: calc(100% - 3px);
        transition: .3s;
    }
    &:hover {
        &::after {
            background-color: transparent;
        }
    }
}
.btn-bg {
    background-color: var(--mainColor);
    border: 1px solid var(--mainColor);
    padding: 25px 77.6px;
    flex-shrink: 0;
    font-size: 20px;
    color: var(--darkWhite);
    &:hover {
        background-color: transparent;
        color: var(--mainColor);
    }
}
@media(max-width: 500px) {
    .btn-bg {
        padding: 17px 60px;
    }
}

.btn-delete {
    position: absolute;
    top: 30px;
    right: 30px;
    z-index: 2;
}
@media(max-width: 1600px) {
    .btn-delete {
        top: 20px;
        right: 20px;
    }
}